taticClass:"row justify-content-center"},[_c('div',{staticClass:"col-lg-9 content-handle"},[_c('h4',{staticClass:"title-blog",staticStyle:{"padding-top":"10% !important"}},[_vm._v("Ti l ai?")]),_c('p',{staticClass:"font-handel text-multi"},[_vm._v(" Cho cc bn! ")]),_c('p',{staticClass:"font-handel text-multi"},[_vm._v(" Ti l Tun,  tt nghip C nhn Cng ngh thng tin ti Hc vin Qun l Gio dc c gn 2 nm. Hin ti ang l lp trnh vin .Net Full-Stack ti mt cng ty v cng ngh  H Ni. Cng vic hng ngy ca ti l g th chc cc bn cng bit ri y (thng th 40% thi gian xem youtube, 10% codding v ti dnh phn nh thi gian cn li  fix bug). ")]),_c('p',{staticClass:"font-handel text-multi"},[_vm._v("y l ln u tin ti vit blog. Do kh nng vit lch ca ti kh t nn ti cn ci thin hn mt cht, cng hy vng cc kinh nghim bn thn c c trong qu trnh hc tp v lm vic c th gip ch cho cc bn c  nh theo ngh lp trnh nh ti. ")]),_c('h4',{staticClass:"title-blog padding-title"},[_vm._v("Chng ng tr thnh lp trnh vin ca ti")]),_c('p',{staticClass:"font-handel text-multi"},[_vm._v(" C l bt u t khi cn b. Nm lp 8 ti bt u c to cm hng bi Thomas Romain v Tania Palumbo cng to hnh nhn vt Jeremie Belpois trong phim Code Lyoko. C l cc bn th h ca ti khng cn xa l g b phim ny u nh (Tht ra cho n gn cui nm trung hc ti cn cha bit tn 2 tc gi ca b phim u @@). Ni dung chnh ca b phim ni v mt nhm thiu nin cng i vo th gii o c tn Lyoko  chng li th lc X.A.N.A c mc ch e da n tri t. Nghe thi  thy ho hng ri, cng vi s ngu li ca cc nhn vt, sng to trong cch chin u v s thng minh ca Einstein (y l tn m mi ngi gi Jeremie Belpois). Ti cha b l tp no v ci hnh dng ca my con boss, crep vn cn xut hin mi khi c ai  nhc n lyoko hay iu g tng t. Nhng nh vy vn cha  v y cha 90% lm l do  ti c th xem phim hot hnh m khng cm thy ti li. n by gi vn c ngi ni vi ti: Tht tt v bn c th bit c mnh mong mun g v iu bn thn mnh thc s thch t sm. ng vy, 10% cn li ca ti l mun c chi game, tht l mt iu mong mun tuyt vi nh? ")]),_c('img',{staticStyle:{"border-radius":"8px"},attrs:{"width":"100%","src":"img/for_me/lyoko.jpg"}}),_c('p',{staticClass:"font-handel text-multi"},[_c('br'),_c('br'),_vm._v("Khng nh cc anh ch i trc, mi ngi u rt chm ch tm hiu v tip cn lp trnh t rt sm. Ti th khc, n khi kt thc THPT trong ti cha c khi nim c th no v kin thc CNTT. Ti thi im , ti cha bao gi thc mc website c to ra nh th no? Ti sao game li vn hnh c theo thao tc ca ngi dng? Ti sao khi c qu nhiu x l trong mt thi im m vn c th vn hnh mt m n vy?... Ok, kh lan man ri, kt thc THPT ti quyt nh thi qun s (nhn vy ch ti  tiu chun thi qun i y :)))) ), v trng ti chn l S quan Thng tin. Ti trt trong c 2 nm thi, n thi im  ti khng cn bit thc s mnh nn hc g v lm g sp ti, ti cn d nh s khng tip tc hc na. Nhng tht l duyn s v bn phn khng cho php (b m ti khng c  nh y, vy l b ti quyt nh tm thi s cho i hc ti H Vinh - chuyn ngnh g v kinh t th phi). Ti vn ght my ci s g b, nghe n kinh t  thy khng c cm hng ri. B qua trng con hc i, CNTT im nguyn vng 2 thp thi. y l tin nhn ca con gi ti (con gi ti xinh lm nh! :))))). Vy l ti xch t i hc v thnh hu bi ca con gi mnh trong khi b ti vn mong mun ti vo Vinh (do bn y i hc t nm u, cn ti thi li 1 nm). Duyn phn ca ti v hc vin ni ti tng hc l do \"con gi\" ti to ra. "),_c('br'),_c('br'),_vm._v("Ln n i hc, v hc CNTT nn ng nhin s c mua my tnh t rt sm, ti thy mnh tht sng sut (chi game ht ngy ny qua ngy khc). Ti tng t c mc im trung bnh rt n tng trong sut qung i hc sinh - sinh vin ngay ti hc k u tin (n tng nh no th ty cc bn ngh nh!). Ln n nm 2, sau khi thot khi nhng kin thc b ch v duy vt v duy tm, ti bt u c hc chuyn ngnh, mn u tin l C. n tn khi  ti mi bit vic hin th c dng ch Hello world! ln mn hnh ng l mt k tch. Ti bt u quay li vic hc tp, iu u tin ti lm l coppy code C v ma trn trn cc din n v xong chy trn my mnh, n cui k ngi c thuc v i thi. o, tng th no ch hc lp trnh d tht (ti tng c ci suy ngh n n  y). Mt phn cm hng hc tp ca ti c ly li vo k hc tip theo. Sang k ti, ti c tri nghim mt cch lp trnh hon ton mi trn C++ m gi ti vn gi l cch lp trnh quc dn, y l OOP  lp trnh hng i tng. Tht may mn khi c gio dy chng ti mn ny rt tn tnh, cng l ln u tin ti c lm mt bi tp ln v thuyt trnh thay v vic thi c nhm chn. Nhn  ti xong, ti mit mi tm hiu mi th lin quan v c gng m rng n ti a bng kin thc mnh c, cng vi vic ti hi c rt nhiu v nhng thc mc m bn thn khng th gii quyt, lc th c gii p, lc th c gi link cho ti t c v lm theo. Sau k y, mn ca c ti c 10 im cui k, v ng nhin l im A+ u tin trong lch s thi sinh vin. "),_c('br'),_c('br'),_vm._v(" Nhy mt n nm 3, ti cng  c mt cht bit v lp trnh nn  chn chuyn ngnh v lp trnh nng cao thay v nhng mng khc. Khi  ti c nim am m bt t v cc thut ton, c bit l m ha v k s (cho n hin ti vn vy). Ti quyt nh b tt c thi gian rnh  tm hiu v thc hnh cc thut ton trong phm vi ti nm bt c. N thc s c ch, v tnh iu  khin cho vic x l cc iss ca ti c nhanh nhy hn rt nhiu, t duy v lp trnh cng tt hn ng k (c th do ti c s hnh dung kh tt, v thi THPT v hnh khng gian ti cc tt lun  ci ny khoe thi ch k lin quan g). Ti thi im ny th phn cm hng cn li v hc tp ca ti c ly li (l g th blog sau ti s ni nh! kk). Trong nm 3 ti bt u hc v C# v h sinh thi v lp trnh ca Microsoft, ti nh hng c l mnh tht s thch hp vi n. Kt thc nm 3, s lng A+ ca ti kh nhiu v n  sc ko k tch nm nht ln mc trn kh, mc 2.5 ti tng ngh bn thn s kh t c y  thnh hin thc. Ln nm 4, mi th ti trng i vi ti u thy khng qu kh khn, k cui cng ti quyt nh i lm. Ti chn FPT Software lm im ta u tin v ti cng  t c iu . Mi s bn rn ca ti tr li do ti phi n cty full-time ng thi lm  n tt nghip ti trng. S bn rn y cng l im nh du ti chnh thc tr thnh mt lp trnh vin. "),_c('br'),_c('br'),_vm._v("Mt cu ni ca Michael Jordan: I can accept failure, everyone fails at something. But I cant accept not trying. "),_c('br'),_c('br'),_vm._v("Phn ti ti s ni v cch t hc m ti thy hiu qu. Cm n cc bn v  c! ")]),_c('div',{staticClass:"like"},[_c('div',{staticClass:"fb-like float-right",staticStyle:{"width":"100%"},attrs:{"data-href":"https://tuannv95.github.io/profile/blog/chang_duong_tro_thanh_lap_trinh_vien_cua_toi/","data-action":"like","data-size":"large"}})])])]